*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: var(--fs-base);
  font-family: var(--ff-base);
}

ul {
  list-style: none;
}

.about-box {
  background-color: #ffb8ca;
}

.btn__icon {
  fill: #d41443;
  border: 2px solid #d41443;
}

.is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  transition: opacity .5s, visibility .5s, transform .5s;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: #0003;
  justify-content: center;
  align-items: center;
  transition: opacity .5s, visibility .5s, transform .5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop .modal {
  width: calc(100vw - 30px);
  height: 609px;
  text-align: right;
  opacity: 1;
  visibility: 1;
  pointer-events: initial;
  background: #ffb8ca;
  border-radius: 4px;
  transition: transform .5s;
  transform: scale(1);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.backdrop .modal .modal-x-button {
  cursor: pointer;
  background: #fff;
  border: 1px solid #0000001a;
  border-radius: 50%;
  margin-top: 8px;
  margin-right: 8px;
  padding: 0;
}

.backdrop .modal .modal-x-button .modal-x-icon {
  fill: #000;
  margin: 6px;
  padding: 3px 3px 4px 4px;
  display: block;
}

.products {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.products__label {
  font-size: 21px;
  font-weight: 400;
  font-family: var(--ff-alt);
  color: --amelies-tutu;
  align-items: center;
  margin-top: 121px;
  margin-bottom: 15px;
  display: flex;
}

.products__title {
  color: --blueberry-lemonade;
  justify-content: center;
  align-items: center;
  margin-bottom: 144px;
  font-family: --ff-alt;
  font-size: 36px;
  font-weight: 400;
  display: flex;
}

.products__menu {
  width: calc(100% - 40px);
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  row-gap: 17px;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.products__menu-items {
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  padding: 20px;
